/**
 * CODE
 */
code,
kbd,
pre,
samp {
  font-family: "SFMono-Regular", menlo, monaco, consolas, "Liberation Mono", "Courier New", monospace;
}

code {
  padding: .2rem .4rem;
  font-size: .9rem;
  color: #f04124;
  background-color: #f8f9fa;
  border-radius: 3px;
}

a > code {
  padding: 0;
  color: inherit;
  background-color: inherit;
}

kbd {
  padding: .2rem .4rem;
  font-size: .9rem;
  color: #fff;
  background-color: #222;
  border-radius: 3px;
}

kbd kbd {
  padding: 0;
  font-size: 100%;
  font-weight: 700;
}

pre {
  display: block;
  font-size: .9rem;
  color: #222;
}

pre code {
  padding: 0;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
  border-radius: 0;
}

.pre-scrollable {
  max-height: 340px;
  overflow-y: scroll;
}

.hljs {
  color: #888;
  background-color: #eee;
}

.hljs-tag,
.hljs-name,
.hljs-attribute {
  color: #888;
}

.hljs-number,
.hljs-literal,
.hljs-variable,
.hljs-template-variable,
.hljs-tag .hljs-attr {
  color: #5bc0de;
}

.hljs-string,
.hljs-doctag {
  color: #f04124;
}

/**
 * GRID
 */
.container {
  width: 100%;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 24em) {
  .container {
    max-width: 23.52em;
  }
}

@media (min-width: 36em) {
  .container {
    max-width: 35.28em;
  }
}

@media (min-width: 48em) {
  .container {
    max-width: 47.04em;
  }
}

@media (min-width: 60em) {
  .container {
    max-width: 58.8em;
  }
}

.grid {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
}

.grid__cell {
  flex: 1;
}

.grid--flexCells > .grid__cell {
  display: flex;
}

/* Gutters */
.grid--gutters {
  margin: -1rem 0 1rem -1rem;
}

.grid--gutters > .grid__cell {
  padding: 1rem 0 0 1rem;
}

/* Alignment per row */
.grid--top {
  align-items: flex-start;
}

.grid--bottom {
  align-items: flex-end;
}

.grid--center {
  align-items: center;
}

/* Alignment per cell */
.grid__cell--top {
  align-self: flex-start;
}

.grid__cell--bottom {
  align-self: flex-end;
}

.grid__cell--center {
  align-self: center;
}

/* Justify Content */
.grid--justifyContent {
  justify-content: center;
}

/* Responsive */
.grid--fit > .grid__cell {
  flex: 1;
}

.grid--full > .grid__cell {
  flex: 0 0 100%;
}

.grid--10f2 > .grid__cell {
  flex: 0 0 50%;
}

.grid--10f3 > .grid__cell {
  flex: 0 0 33.3333%;
}

.grid--1of4 > .grid__cell {
  flex: 0 0 25%;
}

@media (min-width: 24em) {
  .grid\+small--fit > .grid__cell {
    flex: 1;
